import React, { useCallback, useRef, useState } from "react";
import { Button, FormGroup } from "mtforms";
import { useIsMutating } from "@tanstack/react-query";
import { useRegister } from "./hooks";
import { dataURLtoFile, infoAlert, successAlert } from "../../../utils";
import { useMultistepForm } from "../../../utils/useMultistepForm";
import { ContactInfo } from "./components/ContactInfo";
import { PictureUpload } from "./components/PictureUpload";
import Base64Image from "../../../components/base64Image";
import { PublicPaths } from "../../../routes/path";
import { useNavigate } from "react-router-dom";

const Register = () => {
	const [formData, setFormData] = useState({});
	const [picture, setPicture] = useState(null);
	const [errors, setErrors] = useState({});
	const [imageUrl, setImageUrl] = useState("");
	const [cameraActive, setCameraActive] = useState(false);
	const [regSuccess, setRegSuccess] = useState(false);
	const webcamRef = useRef(null);
	const { mutate, isSuccess, reset, data } = useRegister();
	const isLoading = useIsMutating();
	const navigate = useNavigate();

	// STUB: capture image & deactivate camera
	const capture = useCallback(() => {
		const imageSrc = webcamRef.current.getScreenshot();
		if (imageSrc) {
			setPicture(imageSrc);
			setFormData({
				...formData,
				file: dataURLtoFile(imageSrc, "image"),
			});
			setCameraActive(false);
			successAlert(`Picture captured!`);
		} else {
			infoAlert(
				`Please wait for camera to load before clicking on capture!`
			);
		}
	}, [webcamRef, formData]);

	// STUB: turn on camera
	const handleCameraClick = (e) => {
		setCameraActive(true);
	};

	const handleChange = (name, value) => {
		setFormData({ ...formData, [name]: value });
	};

	const validationHandler = (name, error) => {
		setErrors({ ...errors, [name]: error });
	};

	// STUB: multistep form hook
	const {
		steps,
		currentStepIndex,
		step,
		isFirstStep,
		isLastStep,
		back,
		next,
	} = useMultistepForm([
		<ContactInfo
			formData={formData}
			errors={errors}
			validationHandler={validationHandler}
			handleChange={handleChange}
		/>,
		<PictureUpload
			webcamRef={webcamRef}
			isLoading={isLoading}
			capture={capture}
			cameraActive={cameraActive}
			handleCameraClick={handleCameraClick}
			picture={picture}
			formData={formData}
		/>,
	]);

	const submitHandler = () => {
		if (!isLastStep) return next();
		if (!formData.file) {
			infoAlert(
				`No picture found.
				Please click on 'Take Picture' to attach your picture to the form`
			);
			return;
		}
		mutate(formData);
	};

	const homeHandler = () => {
		navigate(PublicPaths.HOME);
	};

	if (isSuccess) {
		reset();
		setRegSuccess(true);
		setImageUrl(data?.data.qr_code_b64);
	}

	return (
		<>
			<h1>Let's get you started!</h1>
			{regSuccess ? (
				<div>
					<h2>
						This is the QR image generated for you kindly save it
					</h2>
					<Base64Image base64String={imageUrl} />
					<div style={{ width: "40%", marginLeft: "30%" }}>
						{" "}
						<button className="btn btnYellow" onClick={homeHandler}>
							Go Home
						</button>
					</div>
				</div>
			) : (
				<FormGroup onSubmit={submitHandler}>
					<div
						style={{
							position: "absolute",
							top: ".5rem",
							right: ".5rem",
						}}
					>
						{currentStepIndex + 1} / {steps.length}
					</div>
					{step}
					<div
						style={{
							marginTop: "1rem",
							display: "flex",
							flexDirection: "column",
							gap: ".5rem",
						}}
					>
						<Button
							title={isLastStep ? "Finish" : "Next"}
							size="large"
							type="submit"
							className={isLoading ? "btn-disabled" : ""}
							loading={isLoading === 1}
							disabled={isLoading === 1}
						/>
						{!isFirstStep && (
							<Button
								title="back"
								size="large"
								onClick={back}
								type="button"
								className={
									isLoading ? "btn-disabled" : "btnRedTxt"
								}
							/>
						)}
					</div>
				</FormGroup>
			)}
		</>
	);
};

export default Register;
