import { Input, Select } from "mtforms";
import { useGetLocation } from "../../../Admin/hooks";

import { FormWrapper } from "./FormWrapper";

export function ContactInfo({
	formData,
	handleChange,
	validationHandler,
	errors,
}) {
	const { data } = useGetLocation();
	return (
		<FormWrapper title="Contact Info">
			<Input
				name="first_name"
				label="First Name"
				value={formData["first_name"]}
				onChange={handleChange}
				type="text"
				validationHandler={validationHandler}
				error={errors.first_name}
				required={true}
				size="large"
			/>
			<Input
				name="last_name"
				label="Last Name"
				value={formData["last_name"]}
				onChange={handleChange}
				type="text"
				validationHandler={validationHandler}
				error={errors.last_name}
				required={true}
				size="large"
			/>
			<Input
				name="email"
				label="Email Address"
				value={formData["email"]}
				onChange={handleChange}
				type="email"
				validationHandler={validationHandler}
				error={errors.email}
				required={true}
				size="large"
			/>
			<Select
				name="location_id"
				label="Location"
				value={formData["location_id"]}
				onChange={handleChange}
				type="text"
				validationHandler={validationHandler}
				error={errors.location_id}
				required={true}
				data={data}
				filter={"name"}
				filterValue={"id"}
				size="large"
				className="formInput"
			/>
		</FormWrapper>
	);
}
